.navbar {
  background-color: #333;
  padding: 15px;
  border-radius: 1rem;
  /* display: flex; */
  text-align: center;
  /* justify-content: end; */
}

.navbar ul {
  list-style: none;
  margin: 0;
  padding: 0;
  align-content: space-between;
}

.navbar li {
  display: inline;
}

.navbar a {
  color: white;
  text-decoration: none;
  padding: 10px 20px;
}

.navbar a:hover {
  background-color: #555;
}